import { useState, useEffect } from 'react';
import { Moon, Sun, Palette } from 'lucide-react';

interface Theme {
  id: string;
  name: string;
  description: string;
  preview: string[];
}

const themes: Theme[] = [
  {
    id: 'default',
    name: 'Warm Orange',
    description: 'Bright and energetic',
    preview: ['hsl(25, 95%, 53%)', 'hsl(33, 100%, 94%)', 'hsl(35, 100%, 98%)']
  },
  {
    id: 'ocean',
    name: 'Ocean Blue',
    description: 'Cool and refreshing',
    preview: ['hsl(200, 98%, 45%)', 'hsl(195, 100%, 92%)', 'hsl(195, 100%, 98%)']
  },
  {
    id: 'forest',
    name: 'Forest Green',
    description: 'Natural and calming',
    preview: ['hsl(140, 77%, 42%)', 'hsl(140, 40%, 92%)', 'hsl(140, 40%, 98%)']
  },
  {
    id: 'sunset',
    name: 'Sunset Pink',
    description: 'Warm and vibrant',
    preview: ['hsl(320, 87%, 68%)', 'hsl(340, 100%, 92%)', 'hsl(340, 100%, 98%)']
  },
  {
    id: 'vintage',
    name: 'Vintage Sepia',
    description: 'Classic and elegant',
    preview: ['hsl(35, 45%, 55%)', 'hsl(45, 25%, 85%)', 'hsl(45, 40%, 96%)']
  }
];

export function ThemeSwitcher() {
  const [isDark, setIsDark] = useState(false);
  const [currentTheme, setCurrentTheme] = useState('default');
  const [isOpen, setIsOpen] = useState(false);

  useEffect(() => {
    // Check for saved theme preference or default to 'default'
    const savedTheme = localStorage.getItem('theme') || 'default';
    const savedDarkMode = localStorage.getItem('darkMode') === 'true';
    
    setCurrentTheme(savedTheme);
    setIsDark(savedDarkMode);
    
    applyTheme(savedTheme, savedDarkMode);
  }, []);

  const applyTheme = (theme: string, dark: boolean) => {
    const root = document.documentElement;
    
    // Remove existing theme classes
    themes.forEach(t => {
      root.removeAttribute(`data-theme`);
    });
    
    // Apply new theme
    if (theme !== 'default') {
      root.setAttribute('data-theme', theme);
    }
    
    // Apply dark mode
    if (dark) {
      root.classList.add('dark');
    } else {
      root.classList.remove('dark');
    }
    
    // Save preferences
    localStorage.setItem('theme', theme);
    localStorage.setItem('darkMode', dark.toString());
  };

  const handleThemeChange = (theme: string) => {
    setCurrentTheme(theme);
    applyTheme(theme, isDark);
    setIsOpen(false);
  };

  const toggleDarkMode = () => {
    const newDarkMode = !isDark;
    setIsDark(newDarkMode);
    applyTheme(currentTheme, newDarkMode);
  };

  return (
    <div className="fixed bottom-4 left-4 z-50">
      <div className="flex items-center gap-2">
        {/* Dark mode toggle */}
        <button
          onClick={toggleDarkMode}
          className="p-3 rounded-full bg-card border border-border shadow-lg hover:shadow-xl transition-all duration-300 hover:scale-105"
          aria-label="Toggle dark mode"
        >
          {isDark ? (
            <Sun className="w-5 h-5 text-primary" />
          ) : (
            <Moon className="w-5 h-5 text-primary" />
          )}
        </button>

        {/* Theme switcher */}
        <div className="relative">
          <button
            onClick={() => setIsOpen(!isOpen)}
            className="p-3 rounded-full bg-card border border-border shadow-lg hover:shadow-xl transition-all duration-300 hover:scale-105 flex items-center gap-2"
            aria-label="Change theme"
          >
            <Palette className="w-5 h-5 text-primary" />
          </button>

          {isOpen && (
            <div className="absolute bottom-full left-0 mb-2 w-80 bg-card border border-border rounded-2xl shadow-2xl p-4 animate-in slide-in-from-bottom-2 duration-200">
              <h3 className="text-sm font-medium text-foreground mb-3">Choose Theme</h3>
              
              <div className="grid gap-3">
                {themes.map((theme) => (
                  <button
                    key={theme.id}
                    onClick={() => handleThemeChange(theme.id)}
                    className={`flex items-center gap-3 p-3 rounded-xl transition-all duration-200 hover:bg-muted ${
                      currentTheme === theme.id
                        ? 'bg-primary/10 border border-primary/20'
                        : 'bg-transparent border border-transparent'
                    }`}
                  >
                    {/* Color preview */}
                    <div className="flex gap-1">
                      {theme.preview.map((color, index) => (
                        <div
                          key={index}
                          className="w-4 h-4 rounded-full border border-black/10"
                          style={{ backgroundColor: color }}
                        />
                      ))}
                    </div>
                    
                    {/* Theme info */}
                    <div className="flex-1 text-left">
                      <div className="text-sm font-medium text-foreground">
                        {theme.name}
                      </div>
                      <div className="text-xs text-muted-foreground">
                        {theme.description}
                      </div>
                    </div>
                    
                    {/* Selected indicator */}
                    {currentTheme === theme.id && (
                      <div className="w-2 h-2 rounded-full bg-primary" />
                    )}
                  </button>
                ))}
              </div>
            </div>
          )}
        </div>
      </div>

      {/* Backdrop */}
      {isOpen && (
        <div
          className="fixed inset-0 -z-10"
          onClick={() => setIsOpen(false)}
        />
      )}
    </div>
  );
} 